:root {
    --main-bg-color: #f4e8c1;
    --title-color: #331e38;
    --primary-color: #331e38;
    --secondary-color: #70a0af;
    --tertiary-color: #a0c1b9;
}

* {
      background-color: #f4e8c1;
      background-color: var(--main-bg-color);
}

h1 {
    font-family: 'Major Mono Display';
    color: #331e38;
    color: var(--title-color);
}

.nav-link {
    font-family: 'Cutive Mono';
    color: #331e38;
    color: var(--primary-color);
    text-decoration: none;
}

.active {
    font-family: 'Cutive Mono';
    color: #70a0af;
    color: var(--secondary-color);
    text-decoration: none;
    border-bottom: 1px solid #70a0af;
    border-bottom: 1px solid var(--secondary-color);
}

/* About Styles */
.about-box {
    background-color: #70a0af;
    background-color: var(--secondary-color);
}

.profile-pic {
    max-height: 500px;
    border-radius: 100px;
}

.section-title {
    font-family: 'Major Mono Display';
    color: #331e38;
    color: var(--primary-color);
}

p, h3, ul, a, h5 {
    font-family: 'Cutive Mono';
}

.about-text {
    border-left: 3px solid #70a0af;
    border-left: 3px solid var(--secondary-color);
}

i {
    color: #331e38;
    color: var(--primary-color);
}

textarea, input {
    background-color: white;
}

.form {
    background-color: #a0c1b9;
    background-color: var(--tertiary-color);
}

button {
    background-color: #331e38;
    background-color: var(--primary-color);
    color: white;
    
}

form, label {
    background-color: #a0c1b9;
    background-color: var(--tertiary-color);
    padding: 10px;
    font-family: 'Cutive Mono';
}

.project-box {
    width: 50%;
}

.blue {
    background-color: #a0c1b9;
    background-color: var(--tertiary-color);
}






